import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";

import "bootstrap/dist/css/bootstrap.min.css";

//react-router
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

//toast
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

//firebase
import firebase from "firebase/app";
import "firebase/auth";

//components
import Home from "./pages/Home";
import Signin from "./pages/Signin";
import Signup from "./pages/Signup";
import PageNotFound from "./pages/PageNotFound";
import { UserContext } from "./context/UserContext";
import Footer from "./Layers/Footer";
import Header from "./Layers/Header";
import FireBaseConfig from "./FireBaseConfig/FireBaseConfig";
// we initialize the fire base or config of  the firebase
firebase.initializeApp(FireBaseConfig);


const App = () => {
  const [user, setUser] = useState(null);//this will set the values of the user 

  return (
    //we are creating router for switching the pages; 
    //in the switching we use route for compononents page switch
    <Router>
      <ToastContainer />
      <UserContext.Provider value={{ user,setUser}}>
      <Header/>
    
        <Switch>

          <Route exact path="/" component={Home} />
          <Route exact path="/signin" component={Signin} />
          <Route exact path="/signup" component={Signup} />
          <Route exact path="*" component={PageNotFound} />
          
        </Switch>
       
       
        <Footer/>
      
      </UserContext.Provider>
    </Router>
  );
};

export default App;
